import React, { useState, useEffect, useRef } from "react";
import { 
    registerWidget, 
    IContextProvider 
} from '../uxp';
import jsPDF from "jspdf";
import "jspdf-autotable"; 
import * as XLSX from 'xlsx';
import { 
    useAlert,
    TitleBar, 
    FilterPanel, 
    WidgetWrapper,
    Button,
    Select,
    Loading,
    FormField
} from "uxp/components";
import './approvalstyles.scss'

interface IWidgetProps {
    uxpContext?: IContextProvider,
    instanceId?: string
}

interface Document {
    _id: string;
    ActivityID: string;
    Year: string;
    Status: string;
    MaleValue?:string
    FemaleValue?:string
    ActivityGroup?:string
    ActivityCategory?:string
    Value?:string
    Uploaded?:string
    TransactionID:string
}

const OHSReport: React.FunctionComponent<IWidgetProps> = (props) => {
    const [documents, setDocuments] = useState<Document[]>([]);
    const [loading, setLoading] = useState<boolean>(true);
    const alert = useAlert();

    const [selectedYear, setSelectedYear] = useState<string>(null);

    useEffect(() => {
        fetchDocuments();
    }, [ selectedYear]);
    
    const getYears = () => {
        const currentYear = new Date().getFullYear();
        return Array.from({ length: 5 }, (_, i) => ({
            label: String(currentYear - i),
            value: String(currentYear - i)
        }));
    };
    
    const fetchDocuments = () => {
        setLoading(true);
        props.uxpContext?.executeAction('ESG', 'showOHSReportDocuments', { selectedYear}, {json:true})
            .then((res) => {
                setDocuments(res);
            })
            .catch((error) => {
                alert.show("Failed to fetch documents");
                console.error("Error fetching documents:", error);
            })
            .finally(() => {
                setLoading(false);
            });
    };

    const handleDownloadPDF = async (doc: Document) => {
        try {
            // Fetch detailed report data using TransactionID
            const reportData = await props.uxpContext?.executeAction(
                "ESG", 
                "downloadOHSTransactionReport",  // API to fetch transaction report
                { TransactionID: doc.TransactionID }, 
                { json: true }  // Expect JSON response
            );
    
            if (!reportData || reportData.length === 0) {
                throw new Error("No data received from server");
            }
    
            // Initialize jsPDF
            const pdf = new jsPDF();
            pdf.setFontSize(16);
            pdf.text("OHS Report", 105, 20, { align: "center" });
    
            // Prepare table data
            const tableData = reportData.map((item: any) => [
                item.ActivityID,
                item.ActivityGroup,
                item.ActivityCategory,
                item.January,
                item.February,
                item.March,
                item.April,
                item.May,
                item.June,
                item.July,
                item.August,
                item.September,
                item.October,
                item.November,
                item.December,
                item.Year,
                item.Value || "-",
            ]);
    
            (pdf as any).autoTable({
                startY: 30,
                head: [["Activity ID", "Activity Group", "Activity Category", "Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec", "Year", "Value"]],
                body: tableData,
                theme: "grid",
                styles: { fontSize: 8 },  // Reduce font size
                columnStyles: {
                    0: { cellWidth: 20 },  // Activity ID
                    1: { cellWidth: 30 },  // Activity Group
                    2: { cellWidth: 30 },  // Category
                    3: { cellWidth: 10 },  // Jan
                    4: { cellWidth: 10 },  // Feb
                    5: { cellWidth: 10 },  // Mar
                    6: { cellWidth: 10 },  // Apr
                    7: { cellWidth: 10 },  // May
                    8: { cellWidth: 10 },  // Jun
                    9: { cellWidth: 10 },  // Jul
                    10: { cellWidth: 10 }, // Aug
                    11: { cellWidth: 10 }, // Sep
                    12: { cellWidth: 10 }, // Oct
                    13: { cellWidth: 10 }, // Nov
                    14: { cellWidth: 10 }, // Dec
                    15: { cellWidth: 15 }, // Year
                    16: { cellWidth: 20 }, // Value
                },
                margin: { left: 10, right: 10 },
            });
            // Download PDF
            pdf.save(`OHS_Report_${doc.Year}_${doc.TransactionID}.pdf`);
    
        } catch (error) {
            alert.show("Failed to download report");
            console.error("Error generating report:", error);
        }
    };

    const handleDownloadXLSX = async (doc: Document) => {
        try {
            // Fetch detailed report data using TransactionID
            const reportData = await props.uxpContext?.executeAction(
                "ESG", 
                "downloadOHSTransactionReport",  // API to fetch transaction report
                { TransactionID: doc.TransactionID }, 
                { json: true }  // Expect JSON response
            );
    
            if (!reportData || reportData.length === 0) {
                throw new Error("No data received from server");
            }

            // Prepare data for worksheet
            const data = reportData.map((item: any) => ({
                "Activity ID": item.ActivityID,
                "Activity Group": item.ActivityGroup,
                "Activity Category": item.ActivityCategory,
                "January": item.January,
                "February": item.February,
                "March": item.March,
                "April": item.April,
                "May": item.May,
                "June": item.June,
                "July": item.July,
                "August": item.August,
                "September": item.September,
                "October": item.October,
                "November": item.November,
                "December": item.December,
                "Year": item.Year,
                "Value": item.Value || "-"
            }));

            // Create worksheet
            const worksheet = XLSX.utils.json_to_sheet(data);

            // Auto-adjust column width
            const columnWidths = data.reduce((widths:any, row:any) => {
                Object.keys(row).forEach((key, index) => {
                    // Compare current cell value with existing width
                    const cellValue = String(row[key]);
                    const currentWidth = widths[index] || 10; // Default minimum width
                    widths[index] = Math.max(
                        currentWidth, 
                        key.length,  // Header length
                        cellValue.length  // Cell content length
                    );
                });
                return widths;
            }, []);

            // Apply column widths
            worksheet['!cols'] = columnWidths.map((width:any) => ({ 
                width: width + 2  // Add some extra padding
            }));

            // Create workbook and add worksheet
            const workbook = XLSX.utils.book_new();
            XLSX.utils.book_append_sheet(workbook, worksheet, "OHS Report");

            // Download Excel file
            XLSX.writeFile(workbook, `OHS_Report_${doc.Year}_${doc.TransactionID}.xlsx`);
        } catch (error) {
            alert.show("Failed to download report");
            console.error("Error generating report:", error);
        }
    };

    const renderContent = () => {
        if (loading) {
            return <Loading />;
        }

        if (!documents || documents.length === 0) {
            return (
                <div className="no-documents-message">
                    <p>No documents to approve</p>
                </div>
            );
        }

        return (
            <div className="approval-table-container">
                <div className="table-wrapper">
                    <table className="approval-table">
                        <thead>
                            <tr>
                                <th>Transaction ID</th>
                                <th>Year</th>
                                <th>Status</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {documents.map((doc) => (
                                <tr key={doc._id}>
                                    <td>{doc.TransactionID}</td>
                                    <td>{doc.Year}</td>
                                    <td>{doc.Status}</td>
                                    <td>
                                        <div className="action-buttons">
                                            <Button
                                                title="Download PDF"
                                                onClick={() => handleDownloadPDF(doc)}
                                                className="action-button pdf-button"
                                            />
                                            <Button
                                                title="Download Excel"
                                                onClick={() => handleDownloadXLSX(doc)}
                                                className="action-button xlsx-button"
                                            />
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    };

    return (
        <WidgetWrapper className="social-approval-widget">
            <TitleBar title='OHS Reports' className="title-bar">
                <FilterPanel onClear={() => {
                    setSelectedYear("");
                }}>
                    <FormField>
                        <Select
                            options={getYears()}
                            selected={selectedYear}
                            onChange={(value) => setSelectedYear(value as string)}
                            placeholder="Select Year"
                        />
                    </FormField>
                </FilterPanel>
            </TitleBar>
            {renderContent()}
        </WidgetWrapper>
    );
};

registerWidget({
    id: "OHSReport",
    widget: OHSReport,
    configs: {
        layout: {
            w: 12,
            h: 12,
            minH: 12,
            minW: 12
        }
    }
});

export default OHSReport